import React, { Component } from "react";
import { Link } from "react-router-dom";

class NflTeams extends Component {
  state = {
    divisions: {},
  };

  render() {
    const { teamsData } = this.props;

    // Goes inside the content div of the Nfl component
    return (
      <div>
        {teamsData.map((team) => (
          <div className="card rounded-top teamCard">
            <div
              className="card-header teamCardHeader"
              style={{
                backgroundImage: "url(" + team.logo + ")",
                backgroundColor: "blue",
                backgroundRepeat: "no-repeat",
                backgroundPosition: "50%",
                backgroundSize: "50%",
              }}
            >
              <div className="teamCardLogo float-left">
                <img
                  className="teamCardLogoImage"
                  src={team.logo}
                  alt={team.name}
                />
              </div>
              <h5 className="card-title">{team.city}</h5>
              <h3 className="card-title">{team.name}</h3>
            </div>

            <div className="card-body">
              <Link className="card-link" to="nfl/">
                Team Page
              </Link>
              <Link className="card-link" to="nfl/">
                Roster
              </Link>
              <Link className="card-link" to="nfl/">
                Schedule
              </Link>
              <Link className="card-link" to="nfl/">
                Stats
              </Link>
            </div>
          </div>
        ))}
      </div>
    );
  }
}

export default NflTeams;
